import React, {useState, useLayoutEffect} from 'react';
import './App.scss';
import ToDoItem from './Components/ToDoitems/ToDoitem.js';
import InputDescription from './Components/InputDescription/InputDescription.js'

const useStateWithLocalStorage = localStorageKey => {
    localStorage.clear();
    const [todos, setTodos] = useState(
        localStorage.getItem(localStorageKey) || [{'id': 0, 'description': 'Взяти цього хлопаку на роботу', 'completed': false}]
    );
    console.log(todos);
    useLayoutEffect(() => {
        localStorage.setItem(localStorageKey, todos)
        console.log(localStorage.getItem(localStorageKey))
        // eslint-disable-next-line
    }, [todos]);
    return [todos, setTodos];
};

const App = () => {
    const [value, setValue] = useState('');    
    const [todos, setTodos] = useStateWithLocalStorage('myTodoLocalStorage');
    const onChange = event => {
        setValue(event.target.value);
    }
    const onClick = () =>{
        let newtodos = todos.map(i =>{
            return i;
        });
        newtodos.push({id:todos.length, description: value, completed:false});
        setTodos(newtodos);
        setValue('');
    }
    const ListChange = id =>{
        let newtodos = todos.map(i =>{
            return i;
        });
        const index = newtodos.map(item => item.id).indexOf(id);
        if(newtodos[index].completed === false){
            newtodos[index].completed = true;
        }
        else{
            newtodos[index].completed = false;
        }         
        setTodos(newtodos);
    }
    const deleteDescription = id =>{
        let newtodos = todos.map(i =>{
            return i;
        });
        const index = newtodos.map(item => item.id).indexOf(id);        
        newtodos.splice(index, 1 );
        setTodos(newtodos);
    }

    const activeTasks = todos.filter(task => task.completed === false)
    const completedTasks = todos.filter(task => task.completed === true)

    const finalTasks = [...activeTasks,...completedTasks].map(item => {
        return(
            <ToDoItem
                key = {item.id}
                description = {item.description}
                completed = {item.completed}
                descriptionStyle = {item.completed ? 'description disable': "description active"}
                ListChange = {() => {ListChange(item.id)}}
                deleteDescription = {() => {deleteDescription(item.id)}}
            />
        )
    })

    return(
        <div>
            
            <h1 className="Title">ToDo List</h1>
            <InputDescription
                value = {value}
                onChange = {onChange}
                onClick = {onClick}
            />
            <div className="wrap-list">
                {finalTasks}
            </div>
        </div>
    );
};

export default App;
